@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Assistant&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Assistant", regular;
  font-family: "Poppins", sans-serif;
}

.container {
  margin: 2px 2px;
  padding: 30px;
  width: 425px;
}

@media screen and (max-width: 425px) {
  .container {
    padding: 0;
    margin: 0;
    max-width: 0;
  }
}

@media screen and (min-width: 320px) {
  .container {
    padding: 0;
    margin: 0;
  }
}

.header {
  text-align: right;
  direction: rtl;
  margin-bottom: 20px;
}

.header-logo {
  width: 40vw;
  height: 30vh;
}

@media screen and (min-width: 320px) {
  .header-logo {
    width: 100%;
    height: 35vh;
  }
}

@media screen and (max-width: 425px) {
  .header-logo {
    width: 100%;
    height: 35vh;
  }
}

.footer {
  margin-top: 30px;
  text-align: center;
}

form {
  font-family: Assistant;
  text-align: right;
  direction: rtl;
}

.formControl {
  /*  margin: 20px 50%; */
  color: #606060;
  font-size: 14px;
  letter-spacing: 0;
  line-height: 2;
  /*   width: 35vw; */
}

.form-section {
  margin: 2% 0;
}

label {
  display: block;
  letter-spacing: -0.01em;
}

.textInput {
  width: 40%;
  background-color: transparent;
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  border-color: #606060;
}

.sameLineLabel {
  display: inline-block;
}

.radio {
  display: inline-block;
  text-align: left;
  width: unset;
  font-size: 12px;
  margin: 0 3px 0 8px;
}

.radioLabel {
  margin-right: 5px;
}

.radioTitleLabel {
  margin-left: 15px;
}

.error {
  color: red;
  margin-right: 10px;
}

.labelComment {
  font-size: 12px;
  line-height: 1;
}

.radioWithLabelComment {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-items: center;
          align-items: center;
}

select {
  font-family: Assistant;
  color: #606060;
}

