.form-control-check {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.form-control-check label {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.form-control-check input {
  -webkit-flex: 2 1;
          flex: 2 1;
  /*   height: 20px; */
}

.btn {
  display: inline-block;
  background: #12c2e9;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

@media screen and (min-width: 320px) {
  form {
    padding: 2%;
    margin: 1% 3%;
  }

  .textInput {
    width: 80%;
  }

  select {
    width: 35vw;
    min-height: 3vh;
  }

  .radioTitleLabel {
    margin-left: 5px;
  }
}

@media screen and (max-width: 425px) {
  form {
    padding: 2%;
    margin: 1% 3%;
  }

  .textInput {
    width: 80%;
  }

  select {
    width: 35vw;
    min-height: 3vh;
  }

  .radioTitleLabel {
    margin-left: 5px;
  }
}

